<template>
  <v-container class="fill-height" fluid>
    <v-row align="center">
      <v-col class="mb-5" cols="3"> </v-col>
      <v-col class="mb-5" cols="5">
        <v-btn
          block
          x-large
          style="height: 150px"
          color="green"
          v-on:click="start()"
          dark
          >Start Script</v-btn
        >
      </v-col>
    </v-row>
    <v-spacer></v-spacer>
    <v-row>
      <v-col class="mb-5" cols="3"> </v-col>
      <v-col class="mb-5" cols="5">
        <v-btn
          style="height: 150px"
          block
          x-large
          color="red"
          v-on:click="stop()"
          dark
          >Stop Script</v-btn
        >
      </v-col>
    </v-row>
    <v-spacer></v-spacer>
  </v-container>
</template>

<script>
import { mapActions } from "vuex";

export default {
  data: () => ({
    info: "",
  }),
  computed: {
    ...mapActions(["postStartScript", "postStopScript"]),
  },
  methods: {
    start() {
      // Start Script
      this.$store
        .dispatch("postStartScript")
        .then((response) => {
          console.log(response);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    stop() {
      // Stop Script
      this.$store
        .dispatch("postStopScript")
        .then((response) => {
          console.log(response);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
  },
};
</script>
