/* eslint-disable no-unused-vars */
import api from './api';

export default {
  getPendingImages: ({ commit }) => {
    api
      .get('/_picture_number')
      .then(response => {
        commit('setPendingImages', response.data);
        return response.data;
      })
      .catch(error => {
        console.log(error);
      });
  },
  getImage: ({ commit }) => {
    api
      .get('/_get_image')
      .then(response => {
        commit('setImage', "data:image/png;base64," + response.data);
        return response.data;
      })
      .catch(error => {
        console.log(error);
      });
  },
  getHoseSeries: ({ commit }) => {
    api
      .get('/_hose_series')
      .then(response => {
        commit('setHoseSeries', response.data);
        return response.data;
      })
      .catch(error => {
        console.log(error);
      });
  },
  postBadItem: ({ commit }, reason) => {
    api
      .post('/_bad_button_click', reason)
      .then(response => {
        return response.data;
      })
      .catch(error => {
        console.log(error);
      });
  },
  postNotSureItem: () => {
    api
      .post('/_unsure_button_click')
      .then(response => {
        return response.data;
      })
      .catch(error => {
        console.log(error);
      });
  },
  postGoodItem: () => {
    api
      .post('/_good_button_click')
      .then(response => {
        return response.data;
      })
      .catch(error => {
        console.log(error);
      });
  },
  postHoseSeries: ({ commit }, current_hose_series) => {
    api
      .post("/_hose_series", current_hose_series)
      .then(function (response) {
        commit('setHoseSeries', current_hose_series);
        return response.data;
      })
      .catch(function (error) {
        console.log(error);
      });
  },
  postStartScript: () => {
    api
      .post("/_start_script")
      .then(function (response) {
        return response.data;
      })
      .catch(function (error) {
        console.log(error);
      });
  },
  postStopScript: () => {
    api
      .post("/_stop_script")
      .then(function (response) {
        return response.data;
      })
      .catch(function (error) {
        console.log(error);
      });
  },
  getSettings: ({ commit },url) => {
    api
      .get(url)
      .then(response => {
        return response.data;
      })
      .catch(error => {
        console.log(error);
      });
  },
  postSettings: ({ commit },url) => {
    api
      .post("/_save_settings_file" + url)
      .then((response) => {
        return response.data;
      })
      .catch(function (error) {
        console.log(error);
      });
  },
};
