<template>
  <v-container class="fill-height" fluid>
    <v-row align="center">
      <v-col cols="12" sm="9">
        <div class="text-center">
          <div class="my-2">
            <v-img :src="Image" aspect-ratio="1.7"></v-img>
          </div>
        </div>
      </v-col>
      <v-col cols="12" sm="3">
        <v-row style="height: 150px;" justify="center">
          <div class="my-2">
            <v-btn
              min-width="200"
              v-on:click="defect()"
              x-large
              color="error"
              dark
              >Defect</v-btn
            >
          </div>
        </v-row>
        <v-row style="height: 150px;" justify="center">
          <div class="my-2">
            <v-btn
              min-width="200"
              x-large
              color="primary"
              v-on:click="saveGood()"
              dark
              >No Defect</v-btn
            >
          </div>
        </v-row>
        <v-row style="height: 150px;" justify="center">
          <div class="my-2">
            <v-btn
              min-width="200"
              x-large
              color="normal"
              v-on:click="saveDoubt()"
              dark
              >Not Sure</v-btn
            >
          </div>
        </v-row>
      </v-col>
    </v-row>
    <v-dialog v-model="dialog" width="650px">
      <v-card>
        <v-card-title class="grey darken-2">Defect Codes</v-card-title>
        <v-container>
          <v-row align="center" justify="center" class="mx-1">
            <v-col cols="5">
              <span>Choose the Error Code:</span>
            </v-col>
            <v-col cols="7">
              <v-overflow-btn
                :items="dropdown_font"
                v-model="defect_code"
                label="Please select the defect"
                target="#dropdown-example"
              ></v-overflow-btn>
            </v-col>
          </v-row>
        </v-container>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn x-large color="secondary" @click="dialog = false"
            >Cancel</v-btn
          >
          <v-btn x-large color="primary" v-on:click="saveDefect()">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import Defects from "../assets/defectCodes";
// import api from "../services/api";
import { mapActions, mapState } from "vuex";

export default {
  data: () => ({
    dropdown_font: Defects,
    dialog: false,
    defect_code: "",
    image_path: require("../assets/no-image-found.png"),
  }),
  async created() {
    this.loadImage();
  },
  computed: {
    ...mapState(["PendingImages", "HoseSeries", "Image"]),
    ...mapActions([
      "postGoodItem",
      "postBadItem",
      "postNotSureItem",
      "getImage",
    ]),
  },
  methods: {
    defect() {
      this.dialog = !this.dialog;
    },
    saveDefect() {
      // CALL API TO SAVE DEFECT
      if (!this.defect_code) {
        alert("Please select a defaect code!");
      } else {
        this.$store
          .dispatch(
            "postBadItem",
            this.defect_code + "," + this.HoseSeries // localStorage.getItem("hose_series")
          )
          .then((response) => {
            console.log(response);
            this.loadImage();
          })
          .catch(function(error) {
            console.log(error);
          });
        this.dialog = false;
      }
    },
    saveGood() {
      // CALL API TO SAVE GOOD
      this.$store
        .dispatch("postGoodItem")
        .then((response) => {
          console.log(response);
          this.loadImage();
        })
        .catch(function(error) {
          console.log(error);
        });
      this.dialog = false;
    },
    saveDoubt() {
      this.$store
        .dispatch("postNotSureItem")
        .then((response) => {
          console.log(response);
          this.loadImage();
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    loadImage() {
      this.$store.dispatch("getImage");
    },
  },
};
</script>
