import Vue from 'vue'
import Vuex from 'vuex'
import App from './App.vue'
import router from './router/router'
import vuetify from './plugins/vuetify'
import store from './store'

Vue.config.productionTip = false

Vue.use(Vuex);

new Vue({
  vuetify,
  router,
  store: store,
  render: h => h(App)
}).$mount('#app')
